//Inicialitza tota la funcionalitat de vue
import { createApp } from "vue";
//Importem el template mare (component principal o root)
import App from "./App.vue";

//Invoquem la funci√≥ createApp passant-li el component principal com a argument i aix√≤ ens retorna la nostra app.
const app = createApp(App);
//Invoquem el m√®tode mount de la nostra app passant-li l'ID de l'HTML dins del qual es muntar√† l'aplicaci√≥ vue
app.mount("#app");
